import React, { useState, useContext, useCallback, useEffect } from "react"
import { Switch, Route } from "react-router-dom"

import Styles from "./Scene.module.css"

import { useHttp } from "../../hooks/http.hook"
import { AuthContext } from "../../context/AuthContext"

import Welcome from "./../Welcome/Welcome"
import Profile from "./../Profile/Profile"

const Scene = ({updateData, profiles}) => {
    const { userId } = useContext(AuthContext)
    const { loading, request } = useHttp()
    const [user, setUser] = useState([])

    const getData = useCallback(async () => {
        try {
            const data = await request(`/api/auth/${userId}`, "GET", null)
            setUser(data)
        } catch (e) {}
    }, [userId, request])

    useEffect(() => {
        if (userId !== null) {
            getData()
        }
    }, [getData, userId])

    return(
        <div className={Styles.scene}>
            <Switch>
                <Route path={`/panel/welcome`} exact>
                    {loading ? <div className={Styles.loading}></div> : <Welcome data={user} />}
                </Route>
                {
                    profiles.map(({_id}, i) => {
                        return(
                            <Route key={i} path={`/panel/${_id}`} exact>
                                {loading ? <div className={Styles.loading}></div> : <Profile updateData={updateData} data={profiles[i]} />}
                            </Route>
                        )
                    })
                }
            </Switch>
        </div>
    )

    // if (loading) {
    //     return <div className={Styles.loading}></div>
    // } else {
    //     return(
    //         <div className={Styles.scene}>
    //             <Switch>
    //                 <Route path={`/panel/welcome`} exact>
    //                     <Welcome data={user} />
    //                 </Route>
    //                 {
    //                     profiles.map(({_id}, i) => {
    //                         return(
    //                             <Route key={i} path={`/panel/${_id}`} exact>
    //                                 <Profile updateData={getData} data={profiles[i]} />
    //                             </Route>
    //                         )
    //                     })
    //                 }
    //             </Switch>
    //         </div>
    //     )
    // }
}

export default Scene