import React, { useState, useCallback } from "react"

import Styles from "./Buttons.module.css"
import Overlay from "./../../App.module.css"
import Balance from "./../../atoms/Balance/Balance"

import { useHistory } from "react-router-dom"
import { useHttp } from "../../hooks/http.hook"

const Buttons = ({currentBalance, _id, updateData, name}) => {
    const history = useHistory()
    const { request } = useHttp()

    const [opened, setOpened] = useState(false)
    const [show, setShow] = useState(false)

    const props = {heading: "Изменить данные", API: "/api/profile/change", currentBalance: currentBalance}
    const blocks = [
        {id: 1, type: "text", name: "name", label: "Имя", value: name, disabled: "disabled"},
        {id: 2, type: "number", name: "balance", label: "Сумма"}
    ]

    const deleteItem = useCallback(async (id) => {
        try {
            await request(`/api/profile/delete/${id}`, "DELETE")
            history.push("/panel/welcome")
            updateData()
        } catch (e) {}
    }, [request, history, updateData])

    const openForm = () => {
        setOpened(true)
    }

    const closeForm = () => {
        setOpened(false)
    }

    const showModal = () => {
        setShow(true)
    }

    const closeModal = () => {
        setShow(false)
    }

    return(
        <div className={Styles.btns}>
            <div className={`${Overlay.overlay} ${opened ? Styles.active : !opened}`} onClick={() => closeForm()}></div>
            { opened ? <Balance updateData={updateData} props={props} blocks={blocks} closeForm={closeForm} /> : <div></div> }
            <a href="/" className={Styles.link} onClick={e => {e.preventDefault(); openForm()} }>
                Изменить
            </a>
            <a href="/" className={Styles.delete} onClick={e => {e.preventDefault(); showModal()} }>
                <i className={`material-icons ${Styles.image}`}>delete</i>
            </a>
            <div className={`${Overlay.overlay} ${show ? Styles.active : !show}`} onClick={() => closeModal()}></div>
            <div className={`${Styles.message} ${show ? Styles.active : !show}`}>
                <p className={Styles.text}>Вы уверены, что хотите удалить?</p>
                <a href="/" className={Styles.submit} onClick={e => {e.preventDefault(); deleteItem(_id)}}>Да</a>
                <a href="/" className={Styles.submit} onClick={e => {e.preventDefault(); closeModal()}}>Нет</a>
            </div>
        </div>
    )
}

export default Buttons