import React, { useState, useEffect, useContext, useCallback } from "react"
import Styles from "./Form.module.css"

import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.min.css'
import { useHttp } from '../../hooks/http.hook'
import { useError } from '../../hooks/error.hook'
import { useSuccess } from '../../hooks/success.hook'
import { AuthContext } from '../../context/AuthContext'

const Form = ({updateData, closeForm, blocks, props}) => {
    toast.configure({
        autoClose: 3000,
        draggable: true
    })

    const { token } = useContext(AuthContext)
    const { loading, request, error, clearError } = useHttp()
    const errorMessage = useError()
    const successMessage = useSuccess()
    const [form, setForm] = useState({})

    useEffect(() => {
        errorMessage(error)
        clearError()
    }, [error, errorMessage, clearError])

    const changeHandler = e => {
        setForm({ ...form, [e.target.name]: e.target.value })
    }

    const postData = useCallback(async () => {
        try {
            const data = await request(props.API, "POST", {...form}, {
                Authorization: `Bearer ${ token }`
            })
            successMessage(data.message)
            updateData()
            closeForm()
        } catch (e) {}
    }, [form, request, props.API, token, successMessage, updateData, closeForm]) 

    const escHandler = useCallback((event) => {
        if(event.keyCode === 27) {
            closeForm()
        }
    }, [closeForm])

    const enterHandler = useCallback((event) => {
        if(event.keyCode === 13) {
            postData()
        }
    }, [postData])

    useEffect(() => {
        document.addEventListener("keydown", escHandler, false)
        document.addEventListener("keydown", enterHandler, false)
        return () => {
            document.removeEventListener("keydown", escHandler, false)
            document.removeEventListener("keydown", enterHandler, false)
        }
    }, [escHandler, enterHandler])
    return(
        <div className={Styles.block}>
            <form action="#" className={Styles.form}>
                <h2 className={Styles.heading}>{props.heading}</h2>
                {
                    blocks.map(({id, type, name, label, value, disabled}) => {
                        return(
                            <div key={id} className={Styles.inputBlock}>
                                <input 
                                    type={type}
                                    className={Styles.input}
                                    name={name}
                                    value={value}
                                    placeholder={label}
                                    autoComplete="off"
                                    disabled={disabled}
                                    onChange={changeHandler} />
                            </div>
                        )
                    })
                }
                <div className={loading ? Styles.loading : Styles.buttons}>
                    <a 
                        href="/"
                        className={loading ? Styles.dn : Styles.cancel} 
                        onClick={e => {e.preventDefault(); closeForm()}}>Отменить</a>
                    <a 
                        href="/" 
                        className={loading ? Styles.dn : Styles.save}
                        onClick={e => {e.preventDefault(); postData()}}>Cохранить</a>
                </div>
            </form>
        </div>
    )
}

export default Form