import React, { useState, useContext, useCallback, useEffect } from "react"
import Styles from "./Dashboard.module.css"
import { NavLink } from "react-router-dom"

import { useHttp } from "../../hooks/http.hook"
import { AuthContext } from "../../context/AuthContext"

import Profiles from "./../Profiles/Profiles"
import Topline from "../Topline/Topline"
import Scene from "../Scene/Scene"

const Dashboard = () => { 
    const [open, setOpen] = useState(false)
    const { userId } = useContext(AuthContext)
    const { loading, request } = useHttp()
    const [profiles, setProfiles] = useState([
        {balance: 0, date: 0, name: "", specific: "", _id: ""}
    ])

    const getData = useCallback(async () => {
        try {
            const fetched = await request(`/api/profile/${userId}`, "GET", null)
            setProfiles(fetched)
            
        } catch (e) {}
    }, [userId, request])

    useEffect(() => {
        getData()
    }, [getData])

    const menuOpen = event => {
        event.preventDefault()

        if (open === false){
            setOpen(true)
        } else {
            setOpen(false)
        }
    }

    return(
        <div className={Styles.interface}>
            <nav className={`${Styles.navbar} ${open ? Styles.open : ""}`}>
                <div className={Styles.flexBlock}>
                    <div className={Styles.title}>
                    <NavLink to="/panel/welcome" className={`${Styles.logo} ${open ? Styles.open : ""}`}>Долговая книжка</NavLink>
                        <a href="/" className={Styles.ham} onClick={menuOpen}>
                            <span className={`${Styles.line} ${open ? Styles.open : ""}`}></span>
                            <span className={Styles.line}></span>
                            <span className={`${Styles.line} ${open ? Styles.open : ""}`}></span>
                        </a>
                    </div>
                    <ul className={`${Styles.menu} ${open ? Styles.open : ""}`}>
                        {loading ? <div className={Styles.loading}></div> : <Profiles profiles={profiles} />}
                    </ul>
                </div>
            </nav>
            <div className={Styles.main}>
                <Topline updateData={getData} />
                <Scene updateData={getData} profiles={profiles} />
            </div>
        </div>
    )
}

export default Dashboard