import React, { useState } from "react"
import { NavLink } from "react-router-dom"
import Fuse from "fuse.js"

import Styles from "./Profiles.module.css"

const Profiles = ({profiles}) => {
    const [form, setForm] = useState("")

    const fuse = new Fuse(profiles, {
        keys: [
            "name"
        ]
    })

    const results = fuse.search(form)
    const profilesFiltered = form ? results.map(result => result.item) : profiles

    const changeHandler = ({ currentTarget = {} }) => {
        const { value } = currentTarget
        setForm(value)
    }

    return(
        <>
            <div className={Styles.search}>
                <input type="text" className={Styles.input} name="name" onChange={changeHandler} placeholder="Поиск..." autoComplete="off" />
            </div>
            {
                profilesFiltered.map(({name, _id}, i) => {
                    return(
                        <li key={i} className={Styles.profile}>
                            <NavLink to={`/panel/${_id}`} className={Styles.link}>
                                <i className={`material-icons ${Styles.image}`}>account_circle</i>{name}
                            </NavLink>
                        </li>
                    )
                })
            }
        </>
    )
}

export default Profiles